import config from '../config';
import axios from 'axios';

const prefix = 'api/';
const headers = {
    headers: {
        'Authorization': 'Bearer ' + localStorage.getItem("token"),
    }
}
const onLoginUser = data => {
    return new Promise((resolve, reject) => {

        axios.post(
            `${config.host}${prefix}login`, data, headers
        ).then((response) => {
            resolve(response.data);
        }).catch(function (error) {
            reject(error);
        });
    });
}
const listUsers = search => {
    return new Promise((resolve, reject) => {

        axios.post(
            `${config.host}${prefix}listUsers`, { search }, headers
        ).then((response) => {
            resolve(response.data);
        }).catch(function (error) {
            reject(error);
        });
    });
}
